.mapboxgl-control-container {
    display: none;
}

.dashboard {
    position: absolute;
    display: inline-block;
    width: 100%;   
    top: 0;
    bottom: 0px;
}
  
.dashboard-pannel {
    width: 350px;
    background: #fff;
    box-shadow: 0 0 4px rgba(0,0,0,.15);
    margin: 24px;
    position: absolute;
    right: 0;
    outline: none;
    cursor: auto;
    top: 50px;
    bottom: 0;
    box-sizing: border-box;
}
.recharts-default-legend {
	padding-left: 10px !important;
	text-align: left !important;
  }
body {
  margin: 0;
  padding: 0;
}

::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}

::-webkit-scrollbar-thumb {
  background-color: darkgrey;
  outline: 1px solid slategrey;
  border-radius: 50px;
}
